document.addEventListener( 'DOMContentLoaded', function() {
    const WFSBODY = document.querySelector('body');
    const HIDDENEL = 'hidden-element';

    if(WFSBODY.classList.contains('home-menu')){
        let WebNavHome;

        function homeStart(){
            WebNavHome = new WFSnav({
                conteiner: '#header',
                activeClass: 'active',
                activeClassElement: 'elements',
                elements: 'ul a:not(.wfsv-button)',
                marginNav: 0,
                mobileMarginNav: 0,
                navHeight: true,
                mobileCloseOpenEl: '.open-sidebar-list',
                mobileNavHeight: true,
                mobileMarginNav: 20,
                mobileFinish: 1199,
            })
        }

        homeStart();

        let destroyNavTop = document.querySelector('.wfs-destroy-top');
        let startNavTop = document.querySelector('.wfs-start-top');
    
        if(destroyNavTop){
            destroyNavTop.addEventListener('click', (event) => {
                event.preventDefault();
                WebNavHome.destroy();

                if(!destroyNavTop.classList.contains(HIDDENEL)) destroyNavTop.classList.add(HIDDENEL);
                if(startNavTop.classList.contains(HIDDENEL)) startNavTop.classList.remove(HIDDENEL);
            });
        }
    
        if(startNavTop){
            startNavTop.addEventListener('click', (event) => {
                event.preventDefault();
                homeStart();

                if(destroyNavTop.classList.contains(HIDDENEL)) destroyNavTop.classList.remove(HIDDENEL);
                if(!startNavTop.classList.contains(HIDDENEL)) startNavTop.classList.add(HIDDENEL);
            });
        }
    }

    if(WFSBODY.classList.contains('wfsfixednav')){
        let WebNav, WebFixedNav;

        function startWebNav(){
            WebNav = new WFSnav({
                conteiner: '.wfs-nav-menu',
                activeClass: 'active',
                activeClassElement: 'elements',
                elements: '.nav-evo-demo-list a',
                marginNav: 0,
                mobileMarginNav: 0,
                navHeight: true,
                mobileCloseOpenEl: '.open-sidebar-list'
            })
        }

        startWebNav();

        function startWebFixedNav(){
            WebFixedNav = new WFSfixednav({
                conteiner: '.wfs-nav-menu',
                class: 'start-fixed'
            })
        }

        startWebFixedNav();

        let destroyNav = document.querySelector('.wfs-destroy');
        let startNav = document.querySelector('.wfs-start');
    
        if(destroyNav){
            destroyNav.addEventListener('click', (event) => {
                event.preventDefault();
                WebNav.destroy();

                if(!destroyNav.classList.contains(HIDDENEL)) destroyNav.classList.add(HIDDENEL);
                if(startNav.classList.contains(HIDDENEL)) startNav.classList.remove(HIDDENEL);
            });
        }
    
        if(startNav){
            startNav.addEventListener('click', (event) => {
                event.preventDefault();
                startWebNav();

                if(destroyNav.classList.contains(HIDDENEL)) destroyNav.classList.remove(HIDDENEL);
                if(!startNav.classList.contains(HIDDENEL)) startNav.classList.add(HIDDENEL);
            });
        }

        let destroyNavFixed = document.querySelector('.wfs-destroy-flex');
        let startNavFixed = document.querySelector('.wfs-start-flex');

        if(destroyNavFixed){
            destroyNavFixed.addEventListener('click', (event) => {
                event.preventDefault();
                WebFixedNav.destroy();

                if(!destroyNavFixed.classList.contains(HIDDENEL)) destroyNavFixed.classList.add(HIDDENEL);
                if(startNavFixed.classList.contains(HIDDENEL)) startNavFixed.classList.remove(HIDDENEL);
            });
        }
    
        if(startNavFixed){
            startNavFixed.addEventListener('click', (event) => {
                event.preventDefault();
                startWebFixedNav();

                if(destroyNavFixed.classList.contains(HIDDENEL)) destroyNavFixed.classList.remove(HIDDENEL);
                if(!startNavFixed.classList.contains(HIDDENEL)) startNavFixed.classList.add(HIDDENEL);
            });
        }
    }
});

